body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: rgb(221, 164, 89);
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.Box{
  width: 35%;
  margin:16px auto;
  border:1px solid #eee;
  box-shadow: 0 2px 3px #cccccc ;
  padding: 16px;
  text-align: center;
  background-color: rgba(183, 222, 231, 0.5);
  font-size: 25px;
  font-weight: bold;
}

@media (max-width: 767px){
  .Box{
      
    width: 80%;
    font-size: 18px;
      
  }
}
.Box1{
    width: 35%;
    margin:10px auto;
